<template>
    <MainFrame>
        <h2 class="ui header">Market</h2>
        <div class="ui form">
            <div class="field filter">
                <p><b>Looking for <SegmentFilter :categories="categories" :companies="companies"/> during</b> <DateRangeControl/> </p>
            </div>
        </div>
        <p v-if="loading">Loading</p>
        <div v-else>
            <div class="graphs">
                <div id="brand_appearance"></div>
                <div id="brand_share"></div>
            </div>
            <div class="graphs">
                <div id="category_appearance"></div>
                <div id="category_share"></div>
            </div>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "../MainFrame";
    import SegmentFilter from "./SegmentFilter";
    import mockData from '../../mock/market';
    import Plotly from 'plotly.js';
    import {plotAppearance, plotShare} from "../../diagrams";
    import DateRangeControl from "../common/DateRangeControl";
    export default {
        name: "Market",
        components: {DateRangeControl, SegmentFilter, MainFrame},
        data(){
            return {
                companies: [{id:1, title:"Adidas"}, {id:2, title:"Nike"}, {id:3, title:"Reebok"}, {id:4, title:"Demix"}, {id:5, title:'Asics'}, {id:6, title:"Puma"}, {id:7, title:"New Balance"}],
                categories: [{id: 1, title: "Running"}, {id: 2, title: "Training"}, {id: 3, title: "Basketball"}, {id: 4, title: "Football"}],
                loading: true,
                data: null,
                minDate: new Date(1e12)
            }
        },
        updated(){
            this.plotCategoryAppearance();
            this.plotBrandAppearance();
            this.plotCategoryShare();
            this.plotBrandShare();
        },
        mounted(){
            this.loading = false;
            this.data = mockData;
        },
        methods:{
            plotCategoryAppearance(){
                Plotly.purge('category_appearance');
                const {data, config, layout} = plotAppearance(this.data.categoryAppearance);
                Plotly.newPlot('category_appearance', data, {...layout, title: 'Category appearances'}, config);
            },
            plotBrandAppearance(){
                Plotly.purge('brand_appearance');
                const {data, config, layout} = plotAppearance(this.data.brandAppearance);
                Plotly.newPlot('brand_appearance', data, {...layout, title: 'Brand appearances'}, config);
            },
            plotCategoryShare(){
                Plotly.purge('category_share');
                const {data, config, layout} = plotShare(this.data.categoryShare);
                Plotly.newPlot('category_share', data, layout, config);
            },
            plotBrandShare(){
                Plotly.purge('brand_share');
                const {data, config, layout} = plotShare(this.data.brandShare);
                Plotly.newPlot('brand_share', data, {...layout, title: 'Brand market share'}, config);
            }
        }

    }
</script>

<style scoped>
    .field.filter p {
        line-height: 40px;
    }
    .field.filter b {
        margin-right: 10px;
    }

    .graphs {
        margin-top: 40px;
        max-width: 100%;
        display: flex;
    }

    .graphs>div {
        flex: 1 0 200px;
        min-width: 0;
    }
</style>