<template>
    <div ref="root" class="root">
        <AddToListIcon @clicked="open()"/>
        <div v-if="isOpen" class="popover">
            <div class="ui vertical menu">
                <a class="item" v-for="item of lists" :key="item.id" @click="select(item.id)">
                    {{item.name}}
                </a>
                <a class="item" :key="'new'">+ create new list</a>
            </div>
        </div>
    </div>

</template>

<script>
    import AddToListIcon from "../../icons/AddToListIcon";
    export default {
        name: "ItemAction",
        components: {AddToListIcon},
        props:{
            lists: Array
        },
        data(){
            return {
                isOpen: false
            }
        },
        methods: {
            onClick(e){
                if(!this.$refs.root.contains(e.target)){
                    this.close()
                }
            },
            close(){
                this.isOpen = false;
                document.removeEventListener('click', this.onClick);
            },
            open(){
                if(!this.isOpen){
                    this.isOpen = true;
                    document.addEventListener('click', this.onClick);
                }
            },
            beforeDestroy(){
                this.close()
            },
            select(id){
                this.close();
                this.$emit('selected', id);
            }
        }
    }
</script>

<style scoped>
    .root {
        position: relative;
    }
    .root svg {
        cursor: pointer;
    }
    .popover {
        position: absolute;
        z-index: 1;
        right:0;
    }

    .popover:before{
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        transform: rotate(45deg);
        background-color: white;
        border: 1px solid rgba(34,36,38,.15);
        border-right: none;
        border-bottom: none;
        position: absolute;
        top: -10px;
        right: 28px;
    }

    .ui.menu {
       font-size: 11px;
    }
</style>