<template>
    <div ref="root" class="root">
        <span  v-for="(chip,i) in getChips()" :key="chip.key">
            <span v-if="i>0"> and </span>
            <a class="ui label">
                {{chip.title}}
                <i class="delete icon" @click="remove(chip.key)"></i>
            </a>
        </span>
        <PlusIcon @clicked="open" class="control"/>
        <div v-if="isOpen" class="popover">
            <form ref="filterForm">
            <div class="ui fluid card">
                <div class="content">
                    <div class="header">SUBSCRIBERS</div>
                </div>
                <div class="content ui grid form">
                    <div class="ui row">
                        <div class="four wide column">
                            Gender:
                        </div>
                        <div class="twelve wide column">
                            <div class="grouped fields">
                                <div class="field">
                                    <div class="ui radio checkbox" @click="$refs.genderA.click()">
                                        <input type="radio" name="gender" tabindex="0"  value="A" class="hidden" ref="genderA">
                                        <label>Any</label>
                                    </div>
                                </div>
                                <div class="field">
                                    <div class="ui radio checkbox" @click="$refs.genderM.click()">
                                        <input type="radio" name="gender" tabindex="2" value="M" class="hidden" ref="genderM">
                                        <label>Male</label>
                                    </div>
                                </div>
                                <div class="field">
                                    <div class="ui radio checkbox" @click="$refs.genderF.click()">
                                        <input type="radio" name="gender" tabindex="3" value="F" class="hidden" ref="genderF">
                                        <label>Female</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column">
                            Age:
                        </div>
                        <div class="twelve wide column">
                            <div class="fields">
                                <div class="eight wide field small">
                                    <input type="number" ref="ageMin">
                                </div>
                                <div class="eight wide field small">
                                    <input type="number" ref="ageMax">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column">
                            Location:
                        </div>
                        <div class="twelve wide column">
                            <div class="field">
                                <input type="text" ref="location"/>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column">
                            Interests similar to:
                        </div>
                        <div class="twelve wide column">
                            <div class="field">
                                <input type="text" ref="interests"/>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column"></div>
                        <div class="twelve wide column">
                            <button class="ui button basic fluid" v-on:click="applyFilter()">
                                apply
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            </form>
        </div>
    </div>
</template>

<script>
    import PlusIcon from "../../icons/PlusIcon";
    import {formatGenderChip, formatRangeChip} from "../../../utility";
    export default {
        name: "SubscriberFilter",
        components: {PlusIcon},
        data(){
            return {
                isOpen: false
            }
        },
        props: ['filter'],
        methods: {
            onClick(e){
                if(!this.$refs.root.contains(e.target)){
                    this.close()
                }
            },
            close(){
                this.isOpen = false;
                document.removeEventListener('click', this.onClick);
            },
            open(){
                if(!this.isOpen){
                    this.isOpen = true;
                    document.addEventListener('click', this.onClick);
                    setTimeout(() => {
                        const {gender = 'A', ageMin = '', ageMax = '', location = '', interests = ''} = this.filter;
                        if (this.$refs[`gender${gender}`]) {
                            this.$refs[`gender${gender}`].click();
                        } else {
                            this.$refs.genderA.click();
                        }
                        this.$refs.ageMin.value = ageMin;
                        this.$refs.ageMax.value = ageMax;
                        this.$refs.location.value = location;
                        this.$refs.interests.value = interests;
                    }, 0);
                }
            },
            beforeDestroy(){
                this.close()
            },
            getChips(){
                const {gender = 'A', ageMin = '', ageMax = '', location= '', interests=''} = this.filter;
                const result = [
                    formatGenderChip(gender),
                    formatRangeChip('age', ageMin, ageMax),
                    location && {key: 'location', title: `location: ${location}`},
                    interests && {key: 'interests', title: `interests: ${interests}`}
                ];
                return result.filter(Boolean);
            },
            applyFilter(){
                this.$emit('change', {
                    gender: this.$refs.filterForm.gender.value,
                    ageMin: this.$refs.ageMin.value,
                    ageMax: this.$refs.ageMax.value,
                    location: this.$refs.location.value,
                    interests: this.$refs.interests.value,
                });
                this.close();
            },
            remove(key){
                const result = Object.entries(this.filter)
                    .filter( ([k]) => !k.startsWith(key))
                    .reduce( (r, [k,v]) => ({...r, [k]: v}), {});
                this.$emit('change', result);
            }
        }
    }
</script>

<style scoped>
    .root {
        position: relative;
        display: inline-block;
    }
    .popover {
        position: absolute;
        z-index: 1;
        right: -70px;
        top: 35px;
        width: 400px;
    }

    .popover:before{
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        transform: rotate(45deg);
        background-color: white;
        border: 1px solid rgba(34,36,38,.15);
        border-right: none;
        border-bottom: none;
        position: absolute;
        top: -10px;
        right: 72px;
        z-index:2;
    }

    .card {
        font-size: 12px;
    }

    .control {
        cursor: pointer;
        position: relative;
        top:8px;
    }

    .ui.grid .row {
        padding: 0.2em 0;
    }

    .row .column:first-child {
        text-align: right;
    }
</style>