<template>
    <div ref="root" class="root">
        <MoreIcon @clicked="open()"/>
        <div v-if="isOpen" class="popover">
            <div class="ui vertical menu">
                <a class="item" @click="select('rename')">Rename</a>
                <a class="item" @click="select('delete')">Delete</a>
                <a class="item" @click="select('pdf')">Download PDF</a>
            </div>
        </div>
    </div>
</template>

<script>
    import MoreIcon from "../../icons/MoreIcon";
    export default {
        name: "Actions",
        components: {MoreIcon},
        data(){
            return {
                isOpen: false
            }
        },
        methods: {
            onClick(e){
                if(!this.$refs.root.contains(e.target)){
                    this.close()
                }
            },
            close(){
                this.isOpen = false;
                document.removeEventListener('click', this.onClick);
            },
            open(){
                if(!this.isOpen){
                    this.isOpen = true;
                    document.addEventListener('click', this.onClick);
                }
            },
            beforeDestroy(){
                this.close()
            },
            select(command){
                this.close();
                this.$emit(command);
            }
        }
    }
</script>

<style scoped>
    .root {
        display: inline-block;
        position: relative;
        top: 10px;
        left: 10px;
    }
    .root svg {
        cursor: pointer;
    }
    .popover {
        position: absolute;
        z-index: 1;
        right:0;
    }

    .popover:before{
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        transform: rotate(45deg);
        background-color: white;
        border: 1px solid rgba(34,36,38,.15);
        border-right: none;
        border-bottom: none;
        position: absolute;
        top: -10px;
        right: 8px;
    }

    .ui.menu {
        font-size: 11px;
    }
</style>