<template>
    <MainFrame>
        <h2 class="ui header">Influencer lists</h2>
        <p v-if="loading">Загрузка...</p>
        <div v-else>
            <table class="ui very basic table">
                <thead>
                <tr>
                    <th width="240"></th>
                    <th width="115" class="sortable" @click="setOrder('influencers')" :class="{active:order==='influencers'}">influencers</th>
                    <th width="115" class="sortable" @click="setOrder('unique_subscribers')" :class="{active:order==='unique_subscribers'}">unique subscribers</th>
                    <th width="115" class="sortable" @click="setOrder('relevant_audience')" :class="{active:order==='relevant_audience'}">relevant audience</th>
                    <th width="115" class="sortable" @click="setOrder('relevant_er')" :class="{active:order==='relevant_er'}">relevant ER</th>
                    <th width="115" class="sortable" @click="setOrder('last_modified')" :class="{active:order==='last_modified'}">last modified</th>
                    <th></th>
                    <th width="90"></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in items" :key="item.id">
                    <td>
                        <router-link :to="{name: 'influencer-list', params:{id: item.id}}">{{item.name}}</router-link>
                    </td>
                    <td>
                        {{item.influencers}}
                    </td>
                    <td>
                        {{shortNumber(item.uniqueSubscribers)}}
                    </td>
                    <td>
                        {{shortNumber(item.relevantAudience)}}
                    </td>
                    <td>
                        {{item.relevantER}}
                    </td>
                    <td>
                        {{item.lastModified}}
                    </td>
                    <td>
                        {{item.notes}}
                    </td>
                    <td>
                        <ItemAction/>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "../MainFrame";
    import ItemAction from "./item-action/ItemAction";
    import mockLists from "../../mock/lists";
    import {shortNumber} from "../../utility";
    export default {
        name: "InfluencerLists",
        components: {ItemAction, MainFrame},
        data(){
          return {
              loading: true,
              items: [],
              order: null
          }
        },
        mounted(){
            this.items = mockLists;
            this.loading = false;
        },
        methods: {
            shortNumber: shortNumber,
            setOrder(newOrder){
                this.order = newOrder;
            }
        },

    }
</script>

<style scoped>
    table.ui.table th{
        font-size: 10px;
        text-align: center;
    }

    table.ui.table {
        font-size: 16px;
        text-align: center;
    }

    table.ui.table td:nth-child(7), table.ui.table td:nth-child(1){
        text-align: left;
    }
    table.ui.table td:nth-child(8){
        vertical-align: middle;
        padding: 0;
    }

    table.ui.table th.sortable {
        cursor: pointer;
    }

    table.ui.table th.active {
        color: #65B7F3;
        border-bottom: 2px solid #BDBDBD;
    }
</style>