<template>
    <span v-if="!edit" @click="onEdit()">
        {{value}}
    </span>
    <span class="ui input" v-else>
        <input type="text" :value="value" @blur="onBlur()" v-on:keyup.enter="onBlur()" ref="input"/>
    </span>
</template>

<script>
    export default {
        name: "EditableText",
        props: ['value'],
        data(){
            return {
                edit: false
            }
        },
        methods:{
            onBlur(){
                this.edit = false;
            },
            onEdit(){
                this.edit = true;
                setTimeout(() => {
                    this.$refs.input && this.$refs.input.focus();
                }, 20);
            }
        }
    }
</script>

