<template>
    <div ref="root" class="root">
        <button @click="open" class="ui button">8 brands in 4 categories</button>
        <div v-if="isOpen" class="popover">
            <div class="ui fluid card">
                <div class="content">
                    <div class="header">MARKET SEGMENT</div>
                </div>
                <div class="content ui grid form">
                    <div class="ui row">
                        <div class="four wide column">
                            Competitors:
                        </div>
                        <div class="twelve wide column">
                            <div class="grouped fields">
                                <div class="field" v-for="option of companies" :key="option.id" @click="toggleCompany(option.id)">
                                    <div class="ui checkbox">
                                        <input type="checkbox" name="companies" tabindex="0" :checked="checkedCompanies.includes(option.id)" :value="option.id" class="hidden">
                                        <label>{{option.title}}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column">
                            Categories:
                        </div>
                        <div class="twelve wide column">
                            <div class="grouped fields">
                                <div class="field" v-for="option of categories" :key="option.id" @click="toggleCategory(option.id)">
                                    <div class="ui checkbox">
                                        <input type="checkbox" name="categories" tabindex="0" :checked="checkedCategories.includes(option.id)" :value="option.id" class="hidden">
                                        <label>{{option.title}}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="ui row">
                        <div class="four wide column"></div>
                        <div class="twelve wide column">
                            <button class="ui button basic fluid" v-on:click="close">
                                apply
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SegmentFilter",
        data(){
            return {
                isOpen: false,
                checkedCompanies: [],
                checkedCategories: []
            }
        },
        props: ['categories', 'companies'],
        methods: {
            onClick(e){
                if(this.$refs.root && !this.$refs.root.contains(e.target)){
                    this.close()
                }
            },
            close(){
                this.isOpen = false;
                document.removeEventListener('click', this.onClick);
            },
            open(){
                if(!this.isOpen){
                    this.isOpen = true;
                    document.addEventListener('click', this.onClick);
                }
            },
            beforeDestroy(){
                this.close()
            },
            toggleCategory(id){
                if(this.checkedCategories.includes(id)){
                    this.checkedCategories = this.checkedCategories.filter(i => i!==id);
                }else{
                    this.checkedCategories = [...this.checkedCategories, id];
                }
            },
            toggleCompany(id){
                if(this.checkedCompanies.includes(id)){
                    this.checkedCompanies = this.checkedCompanies.filter(i => i!==id);
                }else{
                    this.checkedCompanies = [...this.checkedCompanies, id];
                }
            }
        }
    }
</script>

<style scoped>
    .root {
        position: relative;
        display: inline-block;
        line-height: 20px;
    }
    .popover {
        position: absolute;
        z-index: 1;
        right: -70px;
        top: 35px;
        width: 400px;
    }

    .popover:before{
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        transform: rotate(45deg);
        background-color: white;
        border: 1px solid rgba(34,36,38,.15);
        border-right: none;
        border-bottom: none;
        position: absolute;
        top: -10px;
        right: 72px;
        z-index:2;
    }

    .card {
        font-size: 12px;
    }

    .control {
        cursor: pointer;
        position: relative;
        top:6px;
    }

    .ui.grid .row {
        padding: 0.2em 0;
    }

    .row .column:first-child {
        text-align: right;
    }
</style>