<template>
    <MainFrame>
        <h2 class="ui header">Influencer analysis</h2>
        <p v-if="loading">
            Загрузка
        </p>
        <div v-else>
            <p><b>Analyzing <EditableText :value="influencer.name"/> for taget audience with</b> &nbsp;<SubscriberFilter :filter="filter" @change="setFilter($event)"/></p>

            <table class="ui very basic table">
                <thead>
                <tr>
                    <th width="80"></th>
                    <th width="140"></th>
                    <th width="110">subscribers</th>
                    <th width="110">relevant subscribers</th>
                    <th width="110">relevant %</th>
                    <th width="110">relevant ER</th>
                    <th width="110">engaged relevant subscribers</th>
                    <th></th>
                    <th width="80"></th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>
                        <img :src="influencer.logo" class="ui avatar image"/>
                    </td>
                    <td>
                        {{influencer.name}}
                    </td>
                    <td>
                        {{shortNumber(influencer.subscribers)}}
                    </td>
                    <td>
                        {{shortNumber(influencer.relevantSubscribers)}}
                    </td>
                    <td>
                        {{influencer.relevantPercent}}
                    </td>
                    <td>
                        {{influencer.relevantER}}
                    </td>
                    <td>
                        {{shortNumber(influencer.engagedRelevantSubscribers)}}
                    </td>
                    <td>
                        {{influencer.notes}}
                    </td>
                    <td>
                        <ItemAction :lists="lists"/>
                    </td>
                </tr>
                </tbody>
            </table>
            <div class="graphs">
                <div id="audience"></div>
                <div id="demography"></div>
                <div id="locations"></div>
            </div>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "../MainFrame";
    import mockInfluencer from "../../mock/influencer"
    import SubscriberFilter from "../influencer-finder/subscriber-filter/index";
    import ItemAction from "../influencer-finder/item-action/index";
    import mockLists from "../../mock/lists";
    import Plotly from "plotly.js"
    import {shortNumber} from "../../utility";
    import {plotAudience, plotDemography} from "../../diagrams";
    import {DIAGRAM_COLORS} from "../../constants";
    import EditableText from "./EditableText";
    export default {
        name: "InfluencerAnalysis",
        components: {EditableText, ItemAction, SubscriberFilter, MainFrame},
        data(){
            return {
                loading: true,
                influencer: null,
                lists: [],
                filter: {}
            }
        },
        methods: {
            shortNumber: shortNumber,
            setFilter(data){
                this.filter = data;
            },
            plotAudience() {
                Plotly.purge('audience');
                const {data, layout, config} = plotAudience(this.influencer.audience);
                Plotly.newPlot('audience', data, {...layout,  margin: {
                        l: 10,
                        r: 10,
                        b: 40,
                        t: 40,
                        pad: 4
                    }}, config);
            },
            plotLocations() {
                Plotly.purge('locations');
                const data = {
                    x: this.influencer.locations.map(v => v[0]),
                    y: this.influencer.locations.map(v => v[1]),
                    marker: {color: DIAGRAM_COLORS.WINTER},
                    type: 'bar',
                    domain: {
                        column: 0
                    },
                    textinfo: 'none'
                };

                const layout = {
                    showlegend: false,
                    font: {
                        size: 12
                    },
                    margin: {
                        l: 30,
                        r: 30,
                        b: 40,
                        t: 40,
                        pad: 4
                    },
                    title: 'Popular locations'
                };

                const config = {responsive: true, staticPlot: true, displaylogo: false};

                Plotly.newPlot('locations', [data], layout, config);
            },
            plotDemography(){
                Plotly.purge('demography');
                const {config, layout, data} = plotDemography(this.influencer.demography[0], this.influencer.demography[1]);
                Plotly.newPlot('demography', data, {...layout, margin: {
                        l: 40,
                        r: 40,
                        b: 70,
                        t: 40,
                        pad: 4
                    }}, config);
            },
            updatePlot(){
                this.plotAudience();
                this.plotLocations();
                this.plotDemography();
            }
        },
        mounted(){
            this.influencer = mockInfluencer;
            this.loading = false;
            this.lists = mockLists;
        },
        updated(){
            this.updatePlot();
        }
    }
</script>

<style scoped>
    table.ui.table th{
        font-size: 10px;
        text-align: center;
    }

    table.ui.table {
        font-size: 16px;
        text-align: center;
    }

    table.ui.table td:nth-child(8){
        text-align: left;
    }
    table.ui.table td:nth-child(9){
        vertical-align: middle;
        padding: 5px 0 0;
    }

    .graphs {
        margin-top: 70px;
        max-width: 100%;
        display: flex;
    }

    .graphs>div {
        flex: 1 0 200px;
        min-width: 0;
    }


</style>