<template>
    <div class="ui grid centered middle aligned">
        <div class="ui column centered outer center aligned">
            <img src="images/logo.png"/>
            <div class="inner">
                <form class="ui form">
                    <div class="field">
                        <div class="ui small input">
                            <input placeholder="email"/>
                        </div>
                    </div>
                    <div class="field">
                        <div class="ui small input">
                            <input placeholder="password" type="password"/>
                        </div>
                    </div>
                    <button class="ui button basic fluid" v-on:click="login()">
                        login
                    </button>
                </form>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: 'Login',
        data() {
            return {
                input: {
                    username: '',
                    password: ''
                }
            }
        },
        methods: {
            login(){
                this.$emit("authenticated", true);
                this.$router.replace({name: 'influencer-finder'});
                return false;
            }
        }
    }
</script>

<style scoped>
    .grid {
        height: 100%
    }
    .inner {
        max-width: 170px;
        margin: 30px auto 0;
    }
</style>