import Router from 'vue-router'
import Login from './components/Login'
import InfluencerFinder from './components/influencer-finder/InfluencerFinder'
import InfluencerLists from './components/influencer-lists/InfluencerLists'
import InfluencerAnalysis from './components/influencer-analysis/InfluencerAnalysis'
import Category from './components/Category'
import Market from './components/market/Market'
import Product from './components/Product'
import InfluencerList from './components/influencer-list/InfluencerList'

import Vue from "vue";

Vue.use(Router);

export default new Router({
    routes:[
        {
            path: '/',
            redirect:{
                name: "login"
            }
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/influencer-finder',
            name: 'influencer-finder',
            component: InfluencerFinder
        },
        {
            path: '/influencer-finder/:id',
            component: InfluencerAnalysis,
            name: 'influencer-analysis'
        },
        {
            path: '/influencer-lists',
            name: 'influencer-lists',
            component: InfluencerLists
        },
        {
            path: '/influencer-lists/:id',
            name: 'influencer-list',
            component: InfluencerList
        },
        {
            path: '/category',
            name: 'category',
            component: Category
        },
        {
            path: '/market',
            name: 'market',
            component: Market
        },
        {
            path: '/product',
            name: 'product',
            component: Product
        }
    ]
})