<template>
    <input ref="input" type="text"/>
</template>

<script>
    import Lightpick from 'lightpick/lightpick';
    import 'lightpick/css/lightpick.css';

    export default {
        name: "DateRangeControl",
        mounted(){
            this.$nextTick(() => {
                new Lightpick({field: this.$refs.input, singleDate: false})
            })
        }
    }
</script>

<style scoped>
    input {
        display: inline-block;
        max-width: 300px;
    }

</style>