import {DIAGRAM_COLORS} from "./constants";

export function plotDemography(male, female) {

    const dataMale = {
        x: male.map(v => v[0]),
        y: male.map(v => v[1]),
        name: 'Male',
        type: 'bar',
        orientation: 'h',
        marker: {
            color: DIAGRAM_COLORS.BLUE
        }
    };
    const dataFemale = {
        x: female.map(v => -v[0]),
        y: female.map(v => v[1]),
        name: 'Female',
        type: 'bar',
        orientation: 'h',
        marker: {
            color: DIAGRAM_COLORS.ELECTRIC
        }
    };
    const layout = {
        font: {
            size: 12
        },
        margin: {
            l: 25,
            r: 25,
            b: 100,
            t: 40,
            pad: 2
        },
        title: 'Demography',
        barmode: 'relative',
        legend: {
            orientation: 'h'
        },
        xaxis: {
            tickvals: [-10, 0, 10],
            ticktext: [10, 0, 10]
        }
    };
    const config = {responsive: true, staticPlot: true, displaylogo: false};

    return {data: [dataMale, dataFemale], layout, config}
}

export function plotAudience(series) {
    const data = [{
        values: series.map(v => v[0]),
        labels: series.map(v => v[1]),
        marker: {
            colors: Object.values(DIAGRAM_COLORS)
        },
        type: 'pie'
    }];

    const layout = {
        font: {
            size: 12
        },
        margin: {
            l: 50,
            r: 25,
            b: 100,
            t: 40,
            pad: 2
        },
        title: 'Audience',
        legend: {
            orientation: 'h'
        }
    };

    const config = {responsive: true, staticPlot: true, displaylogo: false};

    return {data, layout, config};
}

export function plotAppearance(series) {

    const colors = Object.values(DIAGRAM_COLORS);
    const data = series.map((series,i) => ({
        x: series.map(v => v[1]),
        y: series.map(v => v[0]),
        line: {
            color: colors[i]
        },
        type: 'scatter'
    }));

    const layout = {
        showlegend: false,
        font: {
            size: 11
        },
        margin: {
            l: 30,
            r: 20,
            b: 30,
            t: 30,
            pad: 4
        },
        title: 'Product appearances'
    };

    const config = {responsive: true, staticPlot: true, displaylogo: false};

    return {data, layout, config};

}

export function plotShare(series) {

    const total = series.map(arr => arr.map(v=>v[0]))
        .reduce((arr1, arr2) => {
            const result = [];
            for(let i=0;i<arr1.length;i++){
                result.push(arr1[i]+arr2[i])
            }
            return result;
        });
    const colors = Object.values(DIAGRAM_COLORS);
    const data = series.map((series, si) => ({
        x: series.map(v => v[1]),
        y: series.map((v,i) => 1.0*v[0]/total[i]),
        type: 'bar',
        marker: {
            color: colors[si]
        }
    }));

    const layout = {
        showlegend: false,
        font: {
            size: 11
        },
        margin: {
            l: 40,
            r: 20,
            b: 30,
            t: 30,
            pad: 4
        },
        title: 'Category market share',
        barmode: 'stack',
        yaxis: {
            tickformat: ',.0%',
            range: [0,1]
        }
    };

    const config = {responsive: true, staticPlot: true, displaylogo: false};

    return {config, data, layout};

}