<template>
    <MainFrame>
        <h2 class="ui header">Influencer finder</h2>
        <p v-if="loading">Загрузка...</p>
        <div v-else>
            <p>Searching for influencers with
                <InfluencerFilter :filter="filter.influencer" @change="setInfluencerFilter($event)"/>
                and subscribers with <SubscriberFilter :filter="filter.subscriber" @change="setSubscriberFilter($event)"/></p>
            <table class="ui very basic table">
                <thead>
                    <tr>
                        <th width="80"></th>
                        <th width="160"></th>
                        <th width="115" class="sortable" v-on:click="setOrder('subscribers')" :class="{active:order==='subscribers'}">subscribers</th>
                        <th width="115" class="sortable" v-on:click="setOrder('relevant_subscribers')" :class="{active:order==='relevant_subscribers'}">relevant subscribers</th>
                        <th width="115" class="sortable" v-on:click="setOrder('relevant_percent')" :class="{active:order==='relevant_percent'}">relevant %</th>
                        <th width="115" class="sortable" v-on:click="setOrder('relevant_er')" :class="{active:order==='relevant_er'}">relevant ER</th>
                        <th width="115" class="sortable" v-on:click="setOrder('engaged_relevant')" :class="{active:order==='engaged_relevant'}">engaged relevant subscribers</th>
                        <th></th>
                        <th width="90"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in items" :key="item.id">
                        <td>
                            <img :src="item.logo" class="ui avatar image"/>
                        </td>
                        <td>
                            <router-link :to="{name: 'influencer-analysis', params:{id: item.id}}">{{item.name}}</router-link>
                        </td>
                        <td>
                            {{shortNumber(item.subscribers)}}
                        </td>
                        <td>
                            {{shortNumber(item.relevantSubscribers)}}
                        </td>
                        <td>
                            {{item.relevantPercent}}
                        </td>
                        <td>
                           {{item.relevantER}}
                        </td>
                        <td>
                            {{shortNumber(item.engagedRelevantSubscribers)}}
                        </td>
                        <td>
                            {{item.notes}}
                        </td>
                        <td>
                            <ItemAction :lists="lists"/>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "../MainFrame";
    import mockList from "../../mock/influencers";
    import mockDict from "../../mock/lists";
    import ItemAction from "./item-action/index";
    import InfluencerFilter from "./influencer-filter/index";
    import SubscriberFilter from "./subscriber-filter/index";
    import {shortNumber} from "../../utility";

    export default {
        name: "InfluencerFinder",
        data(){
            return {
                filter: {
                    influencer: {
                        ageMin: 6,
                        ageMax: 56,
                        subsMin: 1000
                    },
                    subscriber: {
                        gender: 'F',
                        interests: 'Health'
                    }
                },
                items: [],
                loading: true,
                order: null,
                lists: mockDict
            }
        },
        methods: {
             async loadItems(){
                this.loading = true;
                await new Promise(r => setTimeout(r, 60));
                this.items = mockList;
                this.loading = false;
            },
            setOrder(order) {
                this.order = order;
            },
            shortNumber: shortNumber,
            setInfluencerFilter(value){
                 this.filter.influencer = value;
            },
            setSubscriberFilter(value){
                 this.filter.subscriber = value;
            }
        },
        mounted() {
            this.loadItems()
        },
        components: {SubscriberFilter, InfluencerFilter, ItemAction, MainFrame}
    }
</script>

<style scoped>
    table.ui.table th{
        font-size: 10px;
        text-align: center;
    }

    table.ui.table td{
        font-size: 16px;
        text-align: center;
    }

    table.ui.table td:nth-child(8){
        text-align: left;
    }
    table.ui.table td:nth-child(9){
        vertical-align: middle;
        padding: 5px 0 0;
    }

    table.ui.table th.sortable {
        cursor: pointer;
    }

    table.ui.table th.active {
        color: #65B7F3;
        border-bottom: 2px solid #BDBDBD;
    }


</style>