<template>
    <MainFrame>
        <p v-if="loading">
            Загрузка
        </p>
        <div v-else>
            <h2 class="ui header">{{campaign.name}} <Actions/></h2>
            <p><b>Targeting users with</b> <SubscriberFilter/></p>

            <div class="graphs">
                <div class="summary">
                    <p>Summary:</p>
                    <p>Influencers: <span class="value">{{campaign.influencers}}</span></p>
                    <p>Unique subscribers: <span class="value">{{campaign.uniqueSubscribers}}</span></p>
                    <p>Relevant subscribers: <span class="value">{{campaign.relevantSubscribers}}</span></p>
                    <p>Total relevant ER: <span class="value">{{campaign.totalRelevantER}}</span></p>
                    <p>Frequency: <span class="value">{{campaign.frequency}}</span></p>
                </div>
                <div id="audience"></div>
                <div id="demography"></div>
                <div id="locations"></div>
                <div id="frequency"></div>
            </div>

            <table class="ui very basic table">
                <thead>
                <tr>
                    <th width="80"></th>
                    <th width="140"></th>
                    <th width="110" class="sortable" v-on:click="setOrder('subscribers')" :class="{active:order==='subscribers'}">subscribers</th>
                    <th width="110" class="sortable" v-on:click="setOrder('relevant_subscribers')" :class="{active:order==='relevant_subscribers'}">relevant subscribers</th>
                    <th width="110" class="sortable" v-on:click="setOrder('engaged_relevant_subscribers')" :class="{active:order==='engaged_relevant_subscribers'}">engaged relevant subscribers</th>
                    <th width="110" class="sortable" v-on:click="setOrder('relevant_er')" :class="{active:order==='relevant_er'}">relevant ER</th>
                    <th width="110" class="sortable" v-on:click="setOrder('unique_subscribers')" :class="{active:order==='unique_subscribers'}">unique subscribers</th>
                    <th></th>
                    <th width="80"></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item of influencers" :key="item.id">
                    <td>
                        <img :src="item.logo" class="ui avatar image"/>
                    </td>
                    <td>
                        {{item.name}}
                    </td>
                    <td>
                        {{shortNumber(item.subscribers)}}
                    </td>
                    <td>
                        {{shortNumber(item.relevantSubscribers)}}
                    </td>
                    <td>
                        {{shortNumber(item.engagedRelevantSubscribers)}}
                    </td>
                    <td>
                        {{item.relevantER}}
                    </td>
                    <td>
                        {{shortNumber(item.uniqueSubscribers)}}
                    </td>
                    <td>
                        {{item.notes}}
                    </td>
                    <td>
                        <RemoveIcon/>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </MainFrame>
</template>

<script>
    import SubscriberFilter from "../influencer-finder/subscriber-filter/index";
    import Actions from "./actions/Actions";
    import RemoveIcon from "../icons/RemoveIcon";
    import MainFrame from "../MainFrame";
    import mockCampaign from '../../mock/category';
    import mockInfluencers from '../../mock/influencers';
    import Plotly from 'plotly.js';
    import {shortNumber} from "../../utility";
    import {DIAGRAM_COLORS} from "../../constants";
    import {plotAudience, plotDemography} from "../../diagrams";

    export default {
        name: "InfluencerList",
        components: {MainFrame, RemoveIcon, Actions, SubscriberFilter},
        data(){
            return {
                loading: true,
                campaign: null,
                influencers: [],
                order: null
            }
        },
        mounted() {
            this.loading = false;
            this.campaign = mockCampaign;
            this.influencers = mockInfluencers.slice(0, 4);
        },
        updated(){
          this.plotDemography();
          this.plotAudience();
          this.plotLocations();
          this.plotFrequency();
        },
        methods: {
            shortNumber: shortNumber,
            plotAudience(){
                Plotly.purge('audience');
                const {data, layout, config} = plotAudience(this.campaign.audienceData);
                Plotly.newPlot('audience', data, layout, config);
            },
            plotDemography(){
                Plotly.purge('demography');
                const {data, layout, config} = plotDemography(this.campaign.demographyData[0], this.campaign.demographyData[1]);
                Plotly.newPlot('demography', data, layout, config);
            },
            plotLocations(){
                Plotly.purge('locations');
                const data = {
                    x: this.campaign.locationsData.map(v => v[1]),
                    y: this.campaign.locationsData.map(v => v[0]),
                    marker: {color: DIAGRAM_COLORS.BLUE},
                    type: 'bar',
                    domain: {
                        column: 0
                    },
                    textinfo: 'none'
                };

                const layout = {
                    showlegend: false,
                    font: {
                        size: 12
                    },
                    margin: {
                        l: 25,
                        r: 25,
                        b: 100,
                        t: 40,
                        pad: 4
                    },
                    title: 'Popular locations'
                };

                const config = {responsive: true, staticPlot: true, displaylogo: false};

                Plotly.newPlot('locations', [data], layout, config);
            },
            plotFrequency(){
                Plotly.purge('frequency');
                const data = {
                    x: this.campaign.frequencyData.map(v => v[1]),
                    y: this.campaign.frequencyData.map(v => v[0]),
                    type: 'bar',
                    domain: {
                        column: 0
                    },
                    textinfo: 'none',
                    marker: {
                        color: DIAGRAM_COLORS.BLUE
                    }
                };

                const layout = {
                    showlegend: false,
                    font: {
                        size: 12
                    },
                    margin: {
                        l: 25,
                        r: 0,
                        b: 100,
                        t: 40,
                        pad: 2
                    },
                    title: 'Frequency'
                };

                const config = {responsive: true, staticPlot: true, displaylogo: false};

                Plotly.newPlot('frequency', [data], layout, config);
            },
            setOrder(order) {
                this.order = order;
            }
        }
    }
</script>

<style scoped>
    table.ui.table th{
        font-size: 10px;
        text-align: center;
    }

    table.ui.table td{
        font-size: 16px;
        text-align: center;
    }

    table.ui.table td:nth-child(8){
        text-align: left;
    }
    table.ui.table td:nth-child(9){
        vertical-align: middle;
        padding: 5px 0 0;
    }

    .graphs {
        margin-top: 20px;
        margin-bottom: 20px;
        max-width: 100%;
        display: flex;
        min-height: 420px;
    }

    .graphs>div {
        flex: 1 0 150px;
        min-width: 0;
    }

    .summary {
        font-weight: bold;
        white-space: nowrap;
    }

    .value {
        float: right;
    }

    table.ui.table th.sortable {
        cursor: pointer;
    }

    table.ui.table th.active {
        color: #65B7F3;
        border-bottom: 2px solid #BDBDBD;
    }

</style>