
export function shortNumber(n){
    if(n>1e6){
        return Math.round(n/1e6) + 'M';
    }else if(n>1000){
        return Math.round(n/1000) + 'K';
    }else{
        return n;
    }
}

export function formatGenderChip(value) {
    const key = 'gender';
    if(value === 'M'){
        return {key, title: 'gender: male'}
    }else if(value === 'F'){
        return {key, title: 'gender: female'}
    }
}

export function formatRangeChip(key, from, to) {
    if(from && to){
        return {key, title: `${key}: ${from}-${to}`}
    }else if(from){
        return {key, title: `${key}: >${from}`}
    }else if(to){
        return {key, title: `${key}: <${to}`}
    }
}

