<template>
    <MainFrame>
        <h2 class="ui header">Category</h2>
        <div class="ui form">
            <div class="field">
                <p><b>Looking for
                    <select name="filter">
                        <option v-for="option of options" :key="option">{{option}}</option>
                    </select>
                    shoes during</b> <DateRangeControl/> </p>
            </div>
        </div>
        <p v-if="loading">Loading</p>
        <div v-else>
            <div class="graphs">
                <div id="appearance"></div>
                <div id="share"></div>
            </div>
            <div class="graphs">
                <div id="demography"></div>
                <div id="related"></div>
            </div>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "./MainFrame";
    import Plotly from 'plotly.js';
    import mockData from '../mock/category2';
    import {plotAppearance, plotDemography, plotShare} from "../diagrams";
    import {DIAGRAM_COLORS} from "../constants";
    import DateRangeControl from "./common/DateRangeControl";
    export default {
        name: "Category",
        components: {DateRangeControl, MainFrame},
        data(){
            return {
                loading: true,
                data: null,
                options: ["Running", "Training", 'Football', "Basketball"],
                minDate: new Date(1e12)
            }
        },
        methods: {
            plotAppearance(){
                Plotly.purge('appearance');
                const {data, layout, config} = plotAppearance(this.data.appearance);
                Plotly.newPlot('appearance', data, layout, config);
            },
            plotShare(){
                Plotly.purge('share');
                const {data, config, layout} = plotShare(this.data.share);
                Plotly.newPlot('share', data, {...layout, title: 'Product market share'}, config);
            },
            plotDemography(){
                Plotly.purge('demography');
                const {data, layout, config} = plotDemography(this.data.demography[0], this.data.demography[1]);
                Plotly.newPlot('demography', data, {...layout, margin: {
                        l: 30,
                        r: 30,
                        b: 30,
                        t: 30,
                        pad: 2
                    }}, config);
            },
            plotRelated(){
                Plotly.purge('related');
                const dataMale = {
                    x: this.data.related.map(v => v[1]),
                    y: this.data.related.map(v => v[0]),
                    type: 'bar',
                    marker: {
                        color: DIAGRAM_COLORS.BLUE
                    }
                };
                const layout = {
                    font: {
                        size: 12
                    },
                    margin: {
                        l: 30,
                        r: 10,
                        b: 30,
                        t: 30,
                        pad: 2
                    },
                    title: 'Related categories',
                    showlegend: false
                };
                const config = {responsive: true, staticPlot: true, displaylogo: false};
                Plotly.newPlot('related', [dataMale], layout, config);
            }
        },
        updated(){
            this.plotShare();
            this.plotAppearance();
            this.plotDemography();
            this.plotRelated();
        },
        mounted(){
            this.loading = false;
            this.data = mockData;
        }
    }
</script>

<style scoped>
    .field p {
        line-height: 40px;
    }
    .field b {
        margin-right: 10px;
    }

    .graphs {
        margin-top: 40px;
        max-width: 100%;
        display: flex;
    }

    .graphs>div {
        flex: 1 0 200px;
        min-width: 0;
    }
    .ui.form select {
        display: inline-block;
        max-width: 150px;
        margin: 0 10px;
    }
</style>