<template>
    <MainFrame>
        <h2 class="ui header">Product</h2>
        <div class="ui form">
            <div class="field">
                <p><b>Looking for running shoes during</b>  <DateRangeControl/></p>
            </div>
        </div>
        <p v-if="loading">Loading</p>
        <div v-else>
            <div class="graphs">
                <div id="appearance"></div>
                <div id="share"></div>
            </div>
            <div class="graphs">
                <div id="feedback"></div>
                <div id="features"></div>
            </div>
        </div>
    </MainFrame>
</template>

<script>
    import MainFrame from "./MainFrame";
    import mockData from '../mock/product';
    import Plotly from 'plotly.js';
    import {plotAppearance, plotShare} from "../diagrams";
    import {DIAGRAM_COLORS} from "../constants";
    import DateRangeControl from "./common/DateRangeControl";
    export default {
        name: "Product",
        components: {DateRangeControl, MainFrame},
        data(){
            return {
                loading: true,
                data: null,
                minDate: new Date(1e12)
            }
        },
        methods: {
            plotAppearance(){
                Plotly.purge('appearance');
                const {data, config, layout} = plotAppearance(this.data.appearance);
                Plotly.newPlot('appearance', data, layout, config);
            },
            plotShare(){
                Plotly.purge('share');
                const {data, layout, config} = plotShare(this.data.share);
                Plotly.newPlot('share', data, {...layout, title: 'Product market share'}, config);
            },
            plotFeedback(){
                Plotly.purge('feedback');
                const colors = Object.values(DIAGRAM_COLORS);
                const data = this.data.feedback.map((series, si) => ({
                    x: series.map(v => v[1]),
                    y: series.map(v => v[0]),
                    type: 'bar',
                    marker: {
                        color: colors[si]
                    }
                }));

                const layout = {
                    showlegend: false,
                    font: {
                        size: 11
                    },
                    margin: {
                        l: 30,
                        r: 20,
                        b: 30,
                        t: 30,
                        pad: 4
                    },
                    title: 'Boot model',
                    barmode: 'relative'
                };

                const config = {responsive: true, staticPlot: true, displaylogo: false};

                Plotly.newPlot('feedback', data, layout, config);
            },
            plotFeatures(){
                Plotly.purge('features');
                const colors = Object.values(DIAGRAM_COLORS);
                const data = this.data.features.map((series, si) => ({
                    theta: series.map(v => v[1]),
                    r: series.map(v => v[0]),
                    type: 'scatterpolar',
                    fill: 'toself',
                    marker: {
                        color: colors[si]
                    }
                }));

                const layout = {
                    showlegend: true,
                    font: {
                        size: 11
                    },
                    margin: {
                        l: 20,
                        r: 20,
                        b: 30,
                        t: 30,
                        pad: 4
                    },
                    polar: {
                      radialaxis: {
                          visible: true,
                          range: [0, 10]
                      }
                    },
                    title: 'Product features',
                };

                const config = {responsive: true, staticPlot: true, displaylogo: false};

                Plotly.newPlot('features', data, layout, config);
            }
        },
        updated(){
            this.plotFeatures();
            this.plotFeedback();
            this.plotShare();
            this.plotAppearance();
        },
        mounted(){
            this.loading = false;
            this.data = mockData;
        }
    }
</script>

<style scoped>
    .field p {
        line-height: 40px;
    }
    .field b {
        margin-right: 10px;
    }

    .graphs {
        margin-top: 40px;
        max-width: 100%;
        display: flex;
    }

    .graphs>div {
        flex: 1 0 200px;
        min-width: 0;
    }
</style>